<template>
  <slide-y-down-transition :duration="600">
<div class="row">
       <div class="col-md-12">
        <div v-for="blog in blogs" :key="blog.id">
          <div v-if="blog.id == id">
      
               <img v-bind:src="'static/blog-img/'+blog.img + '-01.jpg'" class="img-responsive center-block"/>
                <div class="col-md-9">
                <h1>{{blog.title}}</h1>
                <h2>Your guide to life in South Oakville’s ultimate custom quality estate community. </h2>
                <br>
                </div>
                <div class="col-md-3">
                </div>
                
                  <div class="col-md-7">
                <p>Those who appreciate the finer things in life understand that the difference is always in the details. That’s why we’re so excited to announce the launch of this new Kingscrest blog.  </p>
                <br>
                <p>This blog is more than just a celebration of the spectacular homes of Kingscrest — it is also a proud reflection of everything that makes life here so exceptional. </p>
                <br>
                <p>This good life takes many forms. One week it may be  a closer look at some of the exclusive private schools in the neighbourhood. Another may feature a detailed exploration of the endless customization options available in these spectacular estate residences on unprecedented 100’ wide lots. We’ll also be discovering some of the hottest restaurants, boutiques and destinations that make Oakville one of the most refined places to live. </p>
                </div>
                  <div class="col-md-5">
                    <img v-bind:src="'../static/blog-img/'+blog.img + '-02.jpg'" class="img-responsive center-block"/>
                </div>
            </router-link>
          </div>
        </div>
        

        </div>
        </div>
        <div class="row">
        <div class="col-md-12" style="padding-left:28px;padding-right:28px;"> 
            <p>     
                We hope that you’ll join us on this exciting journey of luxurious discovery. Register today to have every new post delivered directly to your inbox. </p>
        </div>
    </div>
        


       </slide-y-down-transition>
</template>

<script>
    import blogs from '@/assets/data/blog.json';
    import {
        SlideYDownTransition,
        FadeTransition
    } from 'vue2-transitions';
  
    export default {
        name: 'Blog',

        data() {
            return {
                msg: 'blog',
                blogs: [],
                id: 1
            }
        },
        mounted() {
            this.blogs = blogs;
        },
        methods: {
     
        }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1,
    h2 {
        font-weight: normal;
        text-align: left;
    }

    h1 {
        font-size: 50px
    }
    
    h2{font-style: italic;
    font-size: 25px;}

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
    p{text-align: left;color: #f6f6f6;}
    @media screen and (max-width:768px){
        h1{font-size: 35px;}
        h2{font-size: 20px;}
    }
</style>


