<template>
  <footer>
      <div class="row">
      <div class="col-md-12">
        <a href="http://fernbrookhomes.com/" target="_blank"><img src="../assets/fernbrook.png" class="img-responsive center-block" width="220"/></a>
                  <p class="pp" @click="showModal">Privacy Policy</p>



    <modal
      v-show="isModalVisible"
      @close="closeModal"
    />
  </div>
    </div>
    

  </footer>
</template>

<script>
    import modal from '@/components/modal.vue';
    export default {
        name: 'Navigation',
    components: {
      modal,
    },
            data () {
      return {
        isModalVisible: false,
      };
    },
    methods: {
      showModal() {
        this.isModalVisible = true;
      },
      closeModal() {
        this.isModalVisible = false;
      }
    }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    img {
        margin-bottom: 20px;
    }

    .pp{cursor: pointer;}
</style>



