<script>
    export default {
        name: 'modal',
        methods: {
            close() {
                this.$emit('close');
            },
        },
    };
</script>
<template>
  <transition name="modal-fade">
    <div class="modal-backdrop">
      <div class="modal center-block" role="dialog" aria-labelledby="modalTitle" aria-describedby="modalDescription">

     
          <h2>Privacy Policy
   </h2>
          <br>
           <p>
With your consent, Fernbrook Homes may collect your personal information to serve you better. By completing or responding to our registration forms, online surveys, information requests or similar , you consent to the collection and use of your personal information.
Fernbrook Homes will not sell or release any of your personal information. Your name, address, and IP (Internet Protocol) address will not be added to any mailing lists, forwarded to any third party vendors, or used in conjunction with any website other than our own without your consent.
Here are some of the ways we may use your personal information: 
To offer you a more personal online experience and superior customer service;
To respond to any inquiries that you may have about our homes or communities, and to provide you with any additional information that you request;
To inform you about any special deals, new phases within our existing communities, or new communities that are being developed;
To learn more about you, as a homebuyer, so that we can better anticipate and satisfy your needs;
To obtain information that will help us determine how visitors use our website, and ultimately guide us in making future enhancements and developments to the site.
</p>
<br class="hidden-xs">
           <br>
            <button
              type="button"
              class="btn-close center-block"
              @click="close"
              aria-label="Close modal"
            >
              OK
            </button>
   


      </div>
    </div>
  </transition>
</template>
<style scoped>
    .modal-backdrop {
        position: fixed;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .modal {
        background: #FFFFFF;
        bottom: auto;
        overflow-x: auto;
        display: flex;
        flex-direction: column;
        width: 50%;
        height: auto;
        padding: 20px;
        top: 20%;

    }

    .modal-fade-enter,
    .modal-fade-leave-active {
        opacity: 0;
    }

    .modal-fade-enter-active,
    .modal-fade-leave-active {
        transition: opacity .5s ease
    }


    .modal-body {
        position: relative;
        padding: 20px 30px;
    }

    .btn-close {
        border: 1px solid #000;
        font-size: 20px;
        padding: 10px;
        cursor: pointer;
        font-weight: bold;
        color: #000000;
        background: transparent;
        width: 200px;
        text-align: center;
        transition: 0.3s;
    }

    .btn-close:hover {
        color: #ffffff;
        background: #000000;
    }

    .btn-green {
        color: white;
        background: #4AAE9B;
        border: 1px solid #4AAE9B;
        border-radius: 2px;
    }

    p {
        color: #000000;
        text-align: left;
        padding: 0 50px;
    }

    h2 {
        color: #000000;
    }

    @media screen and (max-width:1200px) {
        .modal {
            width: 90%;
            height: auto;

        }
    }

    @media screen and (max-width:768px) {
        .modal {
            width: 90%;
            height: auto;
            top: 5%;

        }
        p {
            color: #000000;
            text-align: left;
            padding: 0 20px;
            font-size: 12px;
        }
        .btn-close{
                font-size: 16px;
    padding: 0px;
        }
        
        h2{font-size: 16px;}
    }
    
        @media screen and (max-width:321px) {
       p {
            font-size: 9px;
  padding: 0 3px;
        }
    }

</style>


