<template>
      <slide-y-down-transition :duration="600">

  <div class="row">
    <div class="col-md-4 modelwrapper" v-for="model in models" :key="model.title">
            <!-- <img v-bind:src="'static/img/'+model.thumbnail" class="img-responsive" /> -->
            <div class="model">
              <a :href="`static/pdf/${model.pdf}`" target="_blank">
              <img v-bind:src="'static/renderings/'+model.thumbnail"  class="img-responsive center-block"/>
              <h3 class="title">{{model.title}}</h3>
              <p class="floorplan">view floor plan</p>
              </a>
            </div>
        </div>

  </div>
        </slide-y-down-transition>

</template>

<script>
import data from '@/assets/data/data.json'
import {SlideYDownTransition, FadeTransition} from 'vue2-transitions'

export default {
  name: 'HomeDesigns',
      components:{
    SlideYDownTransition,
    FadeTransition
  },
  data () {
    return {
      models: []
    }
  },
      created() {
      this.models = data;
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



